<template>
    <div>
         <Head headback='false' headto='false'  bol='新增地址'></Head>
         <div class="main">
             <div class="content">
                 <div class="conlist" :class="{'action':unames}">
                     <input type="text" placeholder="请填写你的姓名" v-model="uname">
                 </div>
                 <span v-if="unames">请填写您的姓名</span>
                 <router-link tag='div' to='/Seep' class="conlist">
                     <input  type="text" placeholder="小区/写字楼/学校等" readonly ='readonly' v-model="add">
                 </router-link>
                 <div class="conlist" :class="{'action':messs}" >
                     <input type="text" placeholder="请填写详细送餐地址" v-model="mess">
                 </div>
                 <span v-if="messs">{{s1}}</span>
                 <div class="conlist" :class="{'action':tels}">
                     <input  type="text" placeholder="请填写能够联系到您的手机号" v-model="tel">
                 </div>
                 <span v-if="tels">{{s2}}</span>
                 <div class="conlist" :class="{'action':otels}">
                     <input  type="text" placeholder="备用联系电话(选填)" v-model="otel">
                 </div>
                 <span v-if="otels">{{s3}}</span>
             </div>
             <div class="abtn" :class="{'abtns':btn}" @click="getbtn()">新增地址</div>
             <!-- 弹出框 -->
            <div class="modals" v-if="!warnbol">
                <div class="warn">
                    <span></span>
                    <span></span>
                </div>
                <div class="warn-con">{{warn}}</div>
                <div class="btn" @click="getbtn1()">确定</div>
            </div>
         </div>
    </div>
</template>

<script>
import Head from './../header/Head'
import axios from "axios"
axios.defaults.withCredentials=true
export default {
components: {
    Head
},
mounted () {
  this.add= this.$route.query.name  
  this.geo=this.$route.query.geo
},
data () {
    return {
        uname:'',
        add:'',
        mess:'',
        tel:'',
        otel:'',
        unames:false,
        messs:false,
        tels:false,
        otels:false,
        s1:"",
        s2:'',
        s3:'',
        btn:false,
        warn:'',
        warnbol:true,
        geo:0,
        acc:[],
        obj:{}
    }
},
methods: {
    //点击新增地址按钮，提交数据给后台,根据后台返回的数据情况进行相应操作
    getbtn(){
        axios.post('http://elm.cangdu.org/v1/users/37966/addresses',{tag_type:4,sex:1,poi_type:0,phone_bk:this.otel,tag:'公司',geohash:this.geo,address:this.mess,address_detail:this.add,phone:this.tel,name:this.uname}).then(res=>{
            console.log(res)
            if(res.message=="地址信息错误"){
                this.warnbol=false
                this.warn="地址信息错误"
            }
            if(res.message=="详细地址信息错误"){
                this.warnbol=false
                this.warn="详细地址信息错误"
            }
            if(res.message=="收获手机号错误"){
                this.warnbol=false
                this.warn="收获手机号错误"
            }
            if(res.message=="收货人姓名错误"){
                this.warnbol=false
                this.warn="收货人姓名错误"
            }
            if(res.success=='添加地址成功'){
                this.obj.name=this.add
                this.obj.tel=this.tel
                if(localStorage.getItem('getadd')){
                    this.acc=JSON.parse(localStorage.getItem('getadd'))
                    if(this.acc.length==0){
                        this.acc.unshift(this.obj)
                    }else {
                        var r = this.acc.filter((item)=>{
                            return item.name==this.add&&item.tel==this.tel
                        })
                        if(r.length==0){
                            this.acc.unshift(this.obj)
                        }
                    }
                }
                localStorage.setItem('getadd',JSON.stringify(this.acc))
                this.$router.push({path:'/Shopadd'})
            }
        })
    },
    getbtn1(){
        this.warnbol=true
    }
},
watch: {
    //监听input框的值，如果格式不正确或者没有输入，则警示语出现进行警告
    uname:function () {
        if(this.uname==""){
            this.unames=true
        }else{
            this.unames=false
        }
        if(this.uname!=''&&this.unames==false&&this.mess!=''&&this.messs==false&&this.tel!=''&&this.tels==false){
            this.btn=true
        }else {
            this.btn=false
        }
    },
    mess:function () {
        if(this.mess==''){
            this.messs=true
            this.s1='请详细填写送餐地址'
        }else if(this.mess.length<3){
            this.messs=true
            this.s1='送餐地址太短了，不能辨识'
        }else {
            this.messs=false
        }
        if(this.uname!=''&&this.unames==false&&this.mess!=''&&this.messs==false&&this.tel!=''&&this.tels==false){
            this.btn=true
        }else {
            this.btn=false
        }
    },
    tel:function (){
        var phone=/^[1][3,4,5,7,8][0-9]{9}$/
        if(this.tel==''){
            this.tels=true
            this.s2='手机号不能为空'
        }else if(phone.test(this.tel)){
            this.tels=false
        }else {
            this.tels=true
            this.s2='请输入正确的手机号'
        }
        if(this.uname!=''&&this.unames==false&&this.mess!=''&&this.messs==false&&this.tel!=''&&this.tels==false){
            this.btn=true
        }else {
            this.btn=false
        }
    },
    otel:function(){
        var phone=/^[1][3,4,5,7,8][0-9]{9}$/
        if(this.otel!=''){
            if(phone.test(this.otel)){
            this.otels=false
        }else {
            this.otels=true
            this.s3='请输入正确的手机号'
        }
        }else{
            this.otels=false
        }
    },
}
}
</script>

<style lang='scss' scoped>
.main {
    width: 100%;
    height: 100%;
    padding-top: 0.88rem;
    .content {
        width: 100%;
        height: 100%;
        margin-top: 0.18rem;
        background: white;
        padding-top: 0.18rem;
        .conlist {
            width: 6.98rem;
            height: 0.66rem;
            border: 0.01rem solid #ddd;
            background: #f2f2f2;
            border-radius: 0.04rem;
            margin: 0 auto;
            margin-bottom: 0.18rem;
            position: relative;
            input {
                width: 6.72rem;
                height: 0.36rem;
                border: none;
                background: #f2f2f2;
                position: absolute;
                left: 0.14rem;top:0.16rem;
                font-size: 0.3rem;
            }
        }
        .action{
                border: 0.01rem solid red;
            }
        span {
            width: 6.98rem;
            height: 0.2rem;
            font-size: 0.2rem;
            color: red;
            text-align: left;
            margin: 0 auto;
            line-height: 0.04rem;
            display: block
        }
    }
    .abtn {
        width: 6.98rem;
        height: 0.66rem;
        margin: 0 auto;
        border-radius: 0.06rem;
        background: #4cd964; 
        margin-top: 0.16rem;
        font-size: 0.28rem;
        font-weight: bold;
        line-height: 0.66rem;
        color: #B6EFC0;
    }
    .abtns{
        color: #fff;
    }
    .modals{
      width: 5.62rem;
      height: 4rem;
      position: fixed;
      left: 0.94rem;top: 2rem;
      background: white;
      z-index: 5;
      border-radius: 0.1rem;
      .warn {
          width: 1.4rem;
          height: 1.4rem;
          border-radius: 50%;
          border: 0.07rem solid #f8cb86;
          margin: 0 auto;
          margin-top: 0.28rem;
          span:nth-child(1){
              width: 0.06rem;
              height: 0.7rem;
              background: #f8cb86;
              display: block;
              margin: 0 auto;
              margin-top: 0.2rem;
          }
          span:nth-child(2){
              width: 0.1rem;
              height: 0.1rem;
              border-radius: 50%;
              background: #f8cb86;
              display: block;
              margin: 0 auto;
              margin-top: 0.1rem;
          }
      }
      .warn-con{
          width: 4.26rem;
          height: 0.46rem;
          margin: 0 auto;
          margin-top: 0.38rem;
          font-size: 0.36rem;
      }
      .btn {
          width: 100%;
          height: 0.86rem;
          background: #4cd964;
          margin-top: 0.5rem;
          color: white;
          font-size: 0.46rem;
          line-height: 0.86rem;
          border-bottom-left-radius: 0.1rem;
          border-bottom-right-radius: 0.1rem;
      }
  }
}

</style>



